@import url(https://fonts.googleapis.com/css2?family=Hahmlet:wght@600&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0px;
  padding: 0px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn-submit {
  background-color: #22ef01;
  padding: 10px;
  border-radius: 10px;
  color: #333333;
}

.email {
  padding: 10px !important;
  width: 70%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
  padding-left: 12px;
}

input::placeholder {
  color: rgba(0, 0, 0, 0.4);
  padding-left: 12px;
}

input:focus {
  color: rgba(0, 0, 0, 0.4);
}

.forms {
  width: 100%;
}

.btn-submit {
  width: 30%;
  margin-left: -8px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper {
  max-width: 100%;
  padding: 0.4rem;
  background: white;
  border: 1px solid lightgray;
  border-radius: 8px;
  overflow: hidden;
}
.wrapper:hover,
.wrapper.focus {
  box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
}
.search {
  display: flex;
  height: 1.6em;
  align-items: center;
}
.search input {
  flex: 1 1;
  width: 100%;
  border: 0;
}
.search i {
  color: tomato;
}
:focus {
  outline: none;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-bottom: 1.5em;
  border-top: 1px solid lightgray;
  max-height: 0;
  overflow: hidden;
  overflow-y: auto;
  -webkit-animation: enlarge 0.25s forwards;
          animation: enlarge 0.25s forwards;
}
.list li {
  margin-right: -1.5em;
  margin-left: -1.5em;
  padding: 0 1.5em;
  line-height: 1.5;
  cursor: pointer;
}
.list li:hover {
  background: whitesmoke;
}
.result {
  display: flex;
  align-items: center;
}
.result b {
  margin-right: 0.5em;
}
.result span {
  margin: 0 0.5em;
  width: 1em;
  height: 1em;
  border-radius: 0.5em;
}

#search::-webkit-input-placeholder {
  padding-left: 10px;
  color: black;
}

#search::placeholder {
  padding-left: 10px;
  color: black;
}
@-webkit-keyframes enlarge {
  to {
    max-height: 10em;
  }
}
@keyframes enlarge {
  to {
    max-height: 10em;
  }
}

.css-1773qhf {
  -webkit-padding-start: 0rem;
          padding-inline-start: 0rem;
  -webkit-padding-end: 0rem;
          padding-inline-end: 0rem;
}

.mainfont {
  font-family: "Hahmlet", serif !important;
}

.h-btn:hover {
  color: #3b3a3a;
}



